@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{font-family:"Montserrat", sans-serif}.underline-decoration{width:80px;height:7px;border-radius:30px;background-color:#e85c66}

.home-container{height:100vh;width:100vw;background-color:#fbdc4b;display:grid;overflow:hidden;grid-template:1fr 6fr / 7fr;position:relative;grid-template-areas:"logo" "content"}.logo{grid-area:logo;display:flex;flex-direction:column;justify-content:center;margin-left:5%;font-size:25px}.content{grid-area:content;display:flex;flex-direction:column;align-items:center}.text-typing{padding:20px;font-size:35px}.text-typing p{position:relative;margin:0px;white-space:nowrap;overflow:hidden;-webkit-animation:typing 1.5s steps(26, end) forwards,blink 1.5s infinite;animation:typing 1.5s steps(26, end) forwards,blink 1.5s infinite}@-webkit-keyframes typing{0%{width:0%}100%{width:100%}}@keyframes typing{0%{width:0%}100%{width:100%}}@-webkit-keyframes blink{0%,100%{border-right:2px solid transparent}50%{border-right:2px solid #222}}@keyframes blink{0%,100%{border-right:2px solid transparent}50%{border-right:2px solid #222}}.github-icon-container{display:flex;align-items:flex-end;justify-content:center;height:35vh;opacity:1}.searchbar{margin-top:20px;height:60px;background-color:#353b48;border-radius:30px;padding:10px;position:relative}.search-input{color:white;border:0;outline:0;background:none;width:0;caret-color:transparent;line-height:40px;transition:width 0.4s linear}.searchbar>.search-input{padding:0 10px;width:250px;caret-color:#fbdc4b;transition:width 0.3s linear}.searchbar>.search_icon{background:#353b48}a:hover{cursor:pointer}.search_icon{height:40px;width:40px;float:right;display:flex;justify-content:center;align-items:center;border-radius:50%;text-decoration:none}@media screen and (max-width: 500px){.text-typing{font-size:20px}.searchbar>.search-input{padding:0 10px;width:200px;caret-color:#fbdc4b}}

.error-container{height:100vh;width:100vw;background-color:#fbdc4b;overflow:hidden;display:grid;grid-template:7fr / 7fr;position:relative;grid-template-areas:"content";align-items:center;display:flex;flex-direction:column;justify-content:center}.error-text1{padding-top:30px;font-size:70px;font-family:monospace}.error-text2{font-size:35px;font-family:monospace;position:relative}.btn-custom{margin-top:20px;height:60px;width:300px;background-color:#353b48;border-radius:30px;border-color:transparent;padding:10px;position:relative;color:#fbdc4b;font-size:20px}@media screen and (max-width: 450px){.btn-custom{width:250px;font-size:16px}.error-text2{font-size:25px}}

.repo{padding:10px;-webkit-animation:fadeIn 1s;animation:fadeIn 1s;opacity:1}.repo .repo-name{color:#353b48 !important;font-weight:bold;font-size:25px;cursor:pointer}.repo .repo-name:hover{color:#090a0f !important}.repo .repo-description{opacity:0.8;font-size:13px;margin-top:10px;margin-bottom:10px}.repo .repo-footer{display:flex;font-size:13px;opacity:0.7}.repo .repo-footer .repo-language,.repo .repo-footer .repo-forks,.repo .repo-footer .repo-licence,.repo .repo-footer .repo-updated-on{margin-right:10px}@media screen and (max-width: 768px){.repo .repo-name{font-size:18px}.repo .repo-footer{flex-wrap:wrap}}.circle-language{height:13px;width:13px;border-radius:50%;background-color:blue;display:block;margin-right:7px}

.user-container{background:#fbdc4b;height:100vh;padding:30px}.user-container .container{opacity:1;display:flex}.user-container .container .user-infos-container{padding:20px;height:90vh}.user-container .container .user-infos-container .user-avatar-container{display:flex;align-items:center;justify-content:center;border:black}.user-container .container .user-infos-container .user-avatar-container .user-avatar{border-radius:50%;max-height:300px}.user-container .container .user-infos-container .user-infos{display:flex;flex-direction:column;align-items:center;justify-content:center;padding-top:20px}.user-container .container .user-infos-container .user-infos .user-name{font-size:27px;font-weight:bold;text-align:center}.user-container .container .user-infos-container .user-infos .user-login{font-size:18px;opacity:0.7;text-align:center}.user-container .container .user-infos-container .user-infos .user-bio{font-size:20px;margin-top:25px;text-align:center}.user-container .container .user-infos-container .user-infos .user-followers-container{display:flex;margin-top:15px;flex-direction:column;flex-wrap:wrap;align-items:flex-start}.user-container .container .user-infos-container .user-infos .users-links-container{display:contents;margin-top:30px}.user-container .container .user-infos-container .user-infos .users-links-container .user-email{margin-top:30px}.user-container .container .user-infos-container .user-infos .users-links-container .user-website,.user-container .container .user-infos-container .user-infos .users-links-container .user-email{text-overflow:ellipsis;white-space:nowrap;overflow:hidden;width:100%;margin-top:10px}.user-container .container .user-repos-container{height:90vh;padding:20px 50px}.user-container .container .user-repos-container .user-repos-header{display:flex;justify-content:space-between;align-items:center}.user-container .container .user-repos-container .user-repos-header .text-container{display:flex;flex-direction:column}.user-container .container .user-repos-container .user-repos-header .text-container .text{font-size:35px;font-weight:bold;margin-bottom:5px;color:#353b48}.user-container .container .user-repos-container .search-input-container .search-input{border-radius:10px;padding:8px 40px;color:#353b48;caret-color:#353b48;width:100%;outline:none;border:1px solid rgba(18,24,33,0.5);transition:0.1s all ease-in-out}.user-container .container .user-repos-container .search-input-container ::-webkit-input-placeholder{color:black;opacity:0.4}.user-container .container .user-repos-container .search-input-container :-ms-input-placeholder{color:black;opacity:0.4}.user-container .container .user-repos-container .search-input-container ::placeholder{color:black;opacity:0.4}.user-container .container .user-repos-container .search-input-container .search-input:focus{box-shadow:#353b48 0 2px 8px 0;transition:0.1s all ease-in-out}.user-container .container .user-repos-container .repositories-container{overflow-y:scroll;height:80%;margin-top:20px}.user-container .container .user-repos-container .repositories-container .no-repos{text-align:center}@media screen and (max-width: 768px){.user-container{padding:20px;height:auto}.user-container .container{display:block}.user-container .container .user-infos-container{height:auto;margin-bottom:20px}.user-container .container .user-infos-container .user-avatar-container .user-avatar{max-height:100px}.user-container .container .user-infos-container .user-infos{font-size:16px}.user-container .container .user-infos-container .user-infos .user-name{font-size:18px}.user-container .container .user-infos-container .user-infos .user-login{font-size:16px}.user-container .container .user-infos-container .user-infos .user-bio{font-size:16px}.user-container .container .user-repos-container{padding:20px 30px}.user-container .container .user-repos-container .user-repos-header .text-container .text{font-size:18px}}.spinner{display:flex;align-items:center;justify-content:center;height:100vh;background:#fbdc4b}

